.about {
    padding: 80px 50px;
    background: linear-gradient(135deg, #0f172a, #1e293b);
    color: white;
    text-align: center;
    border-radius: 20px;
}

.about h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.about h2 span {
    color: #3b82f6;
}

.about-content {
    max-width: 800px;
    margin: 0 auto;
    font-size: 1.1rem;
    line-height: 1.8;
    text-align: justify;
}

.about-content strong {
    color: #3b82f6; }